@media only screen and (max-width: 350px) {
  img {
    width: 8!important;
  }
}



@media only screen and (max-width: 260px) {
  img {
    width: 6!important;
  }
}



@media only screen and (max-width: 530px) {
  img {
    max-width: 8!important;
    display: block;
    float: none;
  }
}






